import * as XLSX from "xlsx-js-style";

export function sliceArr<T>(data: T[], size: number) {
    const arr = [];

    for (let i = 0; i < data.length; i += size) {
        arr.push(data.slice(i, i + size));
    }

    return arr;
}

export const cloneDeep: <T>(data: T[]) => T[] = data => {
    return data.map(ele => ({ ...ele }));
};

const changeCellformat = (arr: (string | number)[]) => {
    return arr.map(a => {
        return {
            v: a ?? "",
            t: "s",
            s: {
                alignment: { vertical: "center", horizontal: "center" }
            }
        };
    });
};
/**
 * @param time : 11002400
 * 11002400 포맷을 11:00 ~ 24:00 으로 치환해주는 함수입니다.
 *  */
export const timeFormat = (time: string) => {
    if (!time) return "";

    const newTime1 = time.replace(/[^0-9]/g, "");
    const newTime2 = newTime1.replace(/^(\d{2})(\d{2})(\d{2})(\d{2})$/, "$1:$2 ~ $3:$4");

    return newTime2;
};
// 숫자에 콤마를 추가하는 함수
export const insertComma = (num: number) => {
    // 숫자를 문자열로 변환합니다.
    let numberString = num.toString();

    // 소수점 위치를 찾습니다.
    const decimalIndex = numberString.indexOf(".");
    let integerPart, decimalPart;

    // 정수 부분과 소수 부분을 구분합니다.
    if (decimalIndex !== -1) {
        integerPart = numberString.slice(0, decimalIndex);
        decimalPart = numberString.slice(decimalIndex);
    } else {
        integerPart = numberString;
        decimalPart = "";
    }

    // 뒤부터 세 자리마다 쉼표를 삽입합니다.
    let result = "";
    let count = 0;
    for (let i = integerPart.length - 1; i >= 0; i--) {
        result = integerPart[i] + result;
        count++;
        if (count % 3 === 0 && i !== 0) {
            result = "," + result;
        }
    }

    // 소수 부분을 추가합니다.
    result += decimalPart;

    return result;
};
export const excelDown = ({ title, data, fileName = "" }: { title: string[]; data: any[][]; fileName: string }) => {
    // workbook 생성
    const wb = XLSX.utils.book_new();

    const tableHead = changeCellformat(title);

    const dataCells = data.map(ele => changeCellformat(ele));

    const ws = XLSX.utils.aoa_to_sheet([tableHead, ...dataCells]);

    // 셀넓이
    const wscols = tableHead.map(() => ({ wch: 20 }));
    ws["!cols"] = wscols;

    // 셀높이
    const wsrow = data.map(() => ({ hpt: 30 }));
    wsrow.push({ hpt: 30 });
    ws["!rows"] = wsrow;

    XLSX.utils.book_append_sheet(wb, ws, fileName);
    XLSX.writeFile(wb, fileName + ".xlsx");
};
export const toQueryString = (ob: any) => {
    let b = "";
    for (const key in ob) {
        b += `${key}=${ob[key]}&`;
    }

    return b.slice(0, b.length - 1);
};
export const getCurrentPosition = () => {
    return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(
            position => {
                resolve(position.coords);
            },
            error => {
                reject(error);
            }
        );
    });
};

