<script setup lang="ts">
    import { onUnmounted, onMounted } from "vue";
    const emit = defineEmits<{
        close: [];
        check: [];
    }>();

    withDefaults(
        defineProps<{
            bgNone?: boolean;
            title?: string;
            confirm?: boolean;
            alert?: boolean;
            size?: {
                width?: string;
                height?: string;
            };
        }>(),
        {
            bgNone: false,
            show: false,
            confirm: false,
            alert: false
        }
    );

    onMounted(() => {
        document.body.style.overflow = "hidden";
        document.body.classList.add("has-modal");
    });

    onUnmounted(() => {
        if (document.querySelectorAll(".modal-mask").length === 0) {
            document.body.style.overflow = "auto";
            document.body.classList.remove("has-modal");
        }
    });
</script>

<template>
    <Teleport to="body">
        <Transition>
            <div class="modal-mask" :style="{ backgroundColor: bgNone ? 'transparent' : '' }">
                <section class="modal-content" :style="size" :class="{ 'confirm-content': confirm || alert }">
                    <header class="modal-header">
                        <h3 class="modal-title">
                            <template v-if="!confirm && !alert">
                                {{ title }}
                            </template>
                        </h3>
                        <button type="button" class="close-btn" @click="emit('close')">
                            <VIcon icon="ic_close" />
                        </button>
                    </header>
                    <div class="modal-inner" v-if="!confirm && !alert">
                        <slot></slot>
                    </div>
                    <div class="modal-inner" v-else>
                        <p class="modal-inner-alert">
                            <slot></slot>
                        </p>

                        <div class="text-center" v-if="confirm">
                            <button color="grey" class="cancel mr-10" @click="emit('close')">취소</button>
                            <button class="check" @click="emit('check')">확인</button>
                        </div>
                        <div class="text-center" v-if="alert">
                            <button class="check" @click="emit('close')">확인</button>
                        </div>
                    </div>
                </section>
            </div>
        </Transition>
    </Teleport>
</template>

<style scoped lang="scss">
    .v-enter-active,
    .v-leave-active {
        transition: opacity 0.2s ease-in;
    }

    .v-enter-from,
    .v-leave-to {
        opacity: 0;
    }
    .modal-mask {
        position: fixed;
        height: 100%;
        width: 100%;
        left: 0px;
        top: 0px;
        display: flex;
        align-items: center;
        justify-content: center;
        pointer-events: auto;
        z-index: 31;
        background-color: rgba(0, 0, 0, 0.4);
        .modal-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 15px;
            border-bottom: 1px solid #dbdbdb;
            margin-bottom: 30px;

            .modal-title {
                font-size: 18px;
                color: #111;
            }

            .close-btn {
                font-size: 20px;
            }
        }

        .modal-content {
            border-radius: 12px;
            background-color: #fff;
            display: flex;
            flex-direction: column;
            padding: 20px 30px 30px 30px;
            min-height: 200px;
            min-width: 380px;

            &.confirm-content {
                min-width: 380px;
                min-height: 200px;

                .modal-header {
                    border-bottom: none;
                    margin-bottom: 0;
                }

                .modal-inner {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                }
            }

            .modal-inner {
                flex: 1;
                white-space: pre-wrap;
                &-alert {
                    @include font-style(15);
                    color: #111;
                    line-height: 1.53;

                    :slotted(.text-primary) {
                        color: #197ffa;
                    }
                }
            }
            .text-center {
                button {
                    @include font-style(15);
                    font-weight: 500;
                    border-radius: 8px;
                    width: 96px;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                }
                .cancel {
                    border: 1px solid #333;
                    background: #fff;
                }
                .check {
                    background: #0fae6f;
                    color: #fff;
                }
            }
        }
    }
</style>
